body {
  font-family: "Arial", sans-serif;
  min-width: 320px;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1620px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.navigation__block {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
.navigation__item {
  padding-left: 30px;
}
.breadcrumb {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 0;
}
.breadcrumb li {
  padding: 0;
  margin: 0;
}
.breadcrumb li:not(:last-child) {
  padding-right: 30px;
  position: relative;
}
.breadcrumb li:not(:last-child)::after {
  content: ">";
  position: absolute;
  right: 10px;
}

.pagination {
  list-style: none;
  display: flex;
  padding: 0;
}
.pagination li {
  margin: 0;
  padding: 0 8px;
}

.author__list {
  margin-bottom: 20px;
}

.genre__list {
  margin-bottom: 20px;
}

.book__list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  margin-left: -10px;
  margin-right: -10px;
  flex-wrap: wrap;
}
.book__card {
  width: 25%;
  padding-left: 10px;
  padding-right: 10px;
}
@media (max-width: 1024px) {
  .book__card {
    width: 50%;
  }
}

.footer__container {
  padding-top: 20px;
  padding-bottom: 20px;
}